html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
}

p {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  background-repeat: no-repeat;
}

h2,
h3 {
  text-align: center;
  background-color: #E9C46A;
  border-radius: 1.25rem;
  color: #E76F51;
}

h1 {
  margin: 0;
  text-align: center;
  font-size: 4.5rem;
  font-weight: 700;
  color: var(--blanco);
}

@media (min-width: 768px) {
  h1 {
    line-height: 1.5;
    font-size: 5.2rem;
  }
}
h2 {
  font-size: 5rem;
}

h3 {
  font-weight: lighter;
  font-size: 3rem;
}

body {
  background-color: #264653;
  color: #FFFFFF;
  font-family: "Roboto", sans-serif;
}

.container {
  width: min(80%, 120rem);
  margin: 0 auto;
}

section {
  margin-bottom: 15rem;
}

.mailto {
  text-align: center;
  margin-bottom: 10rem;
}

.mailto__link {
  background-color: #E9C46A;
  color: #E76F51;
  padding: 2rem;
  font-weight: bold;
  border-radius: 20px;
}

@media (min-width: 768px) {
  .mailto__link {
    font-size: 2.5rem;
  }
}
.nav {
  display: flex;
  justify-content: space-around;
  background-color: #2A9D8F;
  padding: 2.5rem;
  align-items: center;
}
.nav .nav__link {
  font-size: 2rem;
  color: #E9C46A;
  font-weight: bold;
}

@media (min-width: 768px) {
  .nav {
    justify-content: flex-end;
    gap: 5rem;
  }
  .nav .nav__link {
    font-size: 2.4rem;
    padding: 0.5rem;
  }
  .nav .nav__link:hover {
    font-weight: lighter;
    color: #F4A261;
  }
}
.header--container {
  align-items: center;
  background-color: #2A9D8F;
}

.header {
  text-align: center;
  padding: 2.5rem 5rem 0 5rem;
}
.header .header__info {
  margin-bottom: 5rem;
}
.header .header__info span {
  font-size: 9rem;
  font-weight: lighter;
}
.header .header__img {
  margin: auto 0;
}

.header__svg img {
  margin-bottom: -1rem;
}

@media (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-evenly;
    gap: 5rem;
  }

  .header__info {
    margin: auto 0;
  }
}
.pages__target {
  background-color: #2A9D8F;
  padding: 2rem;
  border-radius: 10px;
  margin-bottom: 5rem;
  text-align: center;
  box-shadow: 0px 10px 25px 0px rgba(0, 0, 0, 0.4);
  -webkit-box-shadow: 0px 8px 25px 0px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 0px 10px 25px 0px rgba(0, 0, 0, 0.4);
}
.pages__target:last-of-type {
  margin-bottom: 0;
}
.pages__target p {
  background: #2A9D8F;
  box-shadow: 5px 5px 10px #217c71, -5px -5px 10px #33bead;
  color: #FFFFFF;
  display: inline-block;
  padding: 1.5rem;
  border-radius: 5rem;
}

.pages__img {
  filter: grayscale(1);
  border-radius: 1.5rem;
}

@media (min-width: 768px) {
  .pages {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, auto);
    gap: 5rem;
  }
  .pages .pages__target {
    margin: 0;
  }

  .pages h2 {
    grid-column: 1/3;
  }
}
.servicios__info {
  text-align: center;
}

@media (min-width: 768px) {
  .servicios {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }

  .servicios__info {
    /*display: grid;*/
    /*grid-template-rows: 1fr repeat(2, auto);*/
    display: flex;
    flex-direction: column;
  }
  .servicios__info h3 {
    /*grid-row: 2/3;*/
    order: 1;
  }
  .servicios__info img {
    /*margin: auto 0;*/
    order: 0;
  }
  .servicios__info p {
    order: 2;
  }
  .servicios__info ul {
    list-style: square;
    order: 3;
  }
}
.footer {
  background-color: #2A9D8F;
  padding: 0.5rem;
}
.footer p {
  text-align: center;
  padding: 0;
  color: #FFFFFF;
}